/**
 * @file
 * Layout Configuration. Set a modular scale and standardized spacing. Define breakpoints.
 *
 * Copy this file to your theme configuration folder of your local theme or component and adjust its _index file to import.
 */


$rootsize: 100%;    // Assigned to HTML element. 100% = 16px. Referenced as 1rem.
$blh: 1.9rem;  // Base size unit in rem (relative to the HTML root size).
$scale: 1.33333; // Sets an size interval mapped over integers. See https://www.modularscale.com for a demonstration.

$max_width: 1400px;
$legible_inset_width: 800px;

$masthead_height: 60px;
$footer_height: 100px;
$admin_bar_height: 40px;

$proportional_width_unit: (100% - (100/$scale))/2;

$standard_sidebar_width: $proportional_width_unit;
$right_sidebar_width: $standard_sidebar_width * 2;
$left_sidebar_width: $standard_sidebar_width;

$splash_area_desktop_min_height: 700px;

$title_area_outside_padding: rv(2);
$title_area_inside_padding: rv(2);
$title_area_bottom_padding: rv(2);


// Block spacing is a reference variable (in relative value) that determines the spacing between items in a layout.
// Typically used for vertical spacing (e.g. between stacked content boxes).

// Edge padding is similar, defining the horizontal padding on content boxes. Used to ensure
// that content doesn't collapse right to the edge of the viewport. Setting
// these identically will create spacing consistency.

// Mobile spacing tends to be tighter than desktop views.

// Note: Functions using these values should anticipate null values to indicate zero (as opposed to a value
// of 0, which is an integer on the relative scale).

$block_spacing: 1;
$mobile_block_spacing: -2;

$edge_padding_factor: $block_spacing;
$mobile_edge_padding_factor: $mobile_block_spacing;

// Standardized breakpoint for moving from desktop layouts to stacked layouts

$stack_breakpoint: 'med';
$tablet_breakpoint: 'med';
$desktop_breakpoint: 'lrg';
$ultrawide_breakpoint: 'xlrg';

// Less wordy aliases. Keep the others for backwards compatibility.

$stack: $stack_breakpoint;
$tablet: $tablet_breakpoint;
$desktop: $desktop_breakpoint;
$ultrawide: $ultrawide_breakpoint;

/* !BREAKPOINTS */


/*  Values for your responsive design breakpoints. This has now been rethought
    thanks to https://medium.freecodecamp.com/the-100-correct-way-to-do-css-breakpoints-88d6a5ba1862#.rduwqht4g

  336px 	  21em       xsml
  600px 	  37.5em     sml
  900px 	  46.25      med
  1200px    75em       lrg
  1800px	  100em      xlrg   */

$media_type: screen;
$feature: min-width;

$breakpoints: (xsml: 21em, sml: 46.25em, med: 56.25em, lrg: 75em, xlrg: 112.5em, xxlrg: 130em);

/* Modular scale reference

  double-octave    : 4
  pi               : 3.14159265359
  major-twelfth    : 3
