// HEADINGS ------------------------------

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  color: $black;
  font-family: $headingfont;
  font-weight: $headingfw;
  box-sizing: border-box;
  @include single-line-height();

  > a {
    color: $black;
    text-decoration: none; } }

h1,h2,h3 {
  width: 100%; }

h1, .h1 {
  @include font-size(map-get($sizefactors,'h1'));
  margin-top: rv(1);
  margin-bottom: rv(0); }

h2, .h2 {
  @include font-size(map-get($sizefactors,'h2'));
  margin-top: rv(1);
  margin-bottom: rv(-1); }

h3, .h3, %h3 {
  @include font-size(map-get($sizefactors,'h3'));
  margin-top: rv(1);
  margin-bottom: 0;
  text-transform: uppercase; }

h4, .h4 {
  @include font-size(map-get($sizefactors,'h4'));
  margin-top: rv(1);
  margin-bottom: 0; }

h5, .h5 {
  @include font-size(map-get($sizefactors,'h5'));
  margin-top: rv(0);
  font-weight: $normalfw;
  font-style: italic;
  margin-bottom: 0; }

h6, .h6 {
  @include font-size(map-get($sizefactors,'h6'));
  margin-top: rv(0);
  margin-bottom: 0;
  font-weight: $normalfw;
  color: neutral(80); }
