/**
 *  @file _byline.sass
 *  @category 60_site_elements
 *  @theme LOCAL
 *  @description Styles the exhibit byline. */

.byline {
  font-family: $headingfont;
  font-style: unset;
  font-weight: $bold;
  text-decoration: underline;
  text-transform: uppercase;
  font-size: font-value(-2);

  > p {
    font-family: inherit;
    font-style: inherit;
    font-size: inherit;
    margin-bottom: -8px;
    margin-top: rv(0);
    margin-left: 0; } }

.card {
  .byline {
    font-family: $headingfont;
    font-style: unset;
    font-weight: $bold;
    text-decoration: underline;
    text-transform: uppercase;
    font-size: font-value(-2);

    > p {
      font-family: inherit;
      font-style: inherit;
      font-size: inherit;
      margin-bottom: -28px;
      margin-top: rv(0);
      margin-left: 0;
      @include bp($stack) {
        margin-left: rv(1); } } } }
