@mixin dashed-line {
    background: url("../../asset/img/svg/icons/dashes.svg") no-repeat; }

@mixin tilt {
    transform: rotate(-1deg);
    -ms-transform: rotate(-1deg); }

@mixin untilt {
    transform: rotate(1deg);
    -ms-transform: rotate(1deg); }

$borderRadius: rv(-1);

$borderWidth: rv(-5);

$buttonRadius : rv(-3);

