/*
   @file _timeline.sass
   @theme Local
   @description  Loads SASS files for local theme. */

// Timeline Structure


.tl-timeline {
  min-height: 60vh; }

.tl-storyslider {
  min-height: 550px - 164px;  // less the timeline nav

  @include vbp('med') {
    min-height: 800px - 164px; } }

// General containers

.tl-slide .tl-slide-scrollable-container {
  display: flex!important; }

.timeline-cont-wrapper {
  height: 100%;

  @include bp($stack) {
    height: $splash_area_desktop_min_height; } }

// Media container

.tl-slide {
  .tl-slide-content-container {
    .tl-slide-content {
      .tl-media {
        margin-top: 0;
        justify-content: center;
        align-items: center;
        padding-bottom: rv(-5);
        overflow: hidden;
        max-width: 100% !important;
        height: 50%;

        @include bp($desktop) {
          padding-bottom: 0;
          max-width: 50% !important;
          height: 100%; } } } } }


.tl-skinny {
  .tl-slide {
    .tl-slide-content-container {
      .tl-slide-content {
        .tl-media {
          padding-top: 0;
          border: 0;
          flex-shrink: 1;
          height: 50%; } } } } }

.tl-slide-content {
  .tl-media {
    .tl-media-content-container {
      height: 100%;
      width: 100%;
      max-height: 100%;
      border: 4px solid $white;
      border-bottom-right-radius: $borderRadius;
      flex-shrink: 1;
      display: flex;
      flex-direction: column;
      overflow: hidden;

      * {
        height: 100%;
        width: 100%; }

      @include bp($desktop) {
        @include proportional_height(100%,.5625); } // 16:9

      &.portrait {
        @include proportional_height(70%,1.2);
        margin: 0 auto;
        max-width: 100%; } } } }

// Media Image

.tl-media .tl-media-content-container .tl-media-content img {
  height: 100%;
  width: 100% !important;
  filter: grayscale(100%);
  object-fit: cover;
  max-height: initial !important; }

.tl-media-shadow {
  box-shadow: none; }


// Text container
.tl-text-content-container {
  align-self: center;
  text-align: left;
  width: 100%;
  padding: rv(2);
  padding-top: 0;

  @include bp($desktop) {
    padding: rv(2);
    padding-top: rv(2); } }

.tl-text-content-container {
  h2.tl-headline {
    font-size: font-value(1) !important;
    @include bp($stack) {
      font-size: font-value(2) !important; } } }

.tl-slide {
  .tl-slide-content-container {
    .tl-slide-content {
      .tl-text {
        display: flex!important;
        margin-top: 0;
        width: 100%;
        max-width: 100%;
        margin-top: 20px;
        height: 50%;

        @include bp($desktop) {
          width: 50%;
          max-width: 50%;
          height: 100%; } } } } }

.tl-skinny {
  .tl-slide {
    .tl-slide-content-container {
      .tl-slide-content {
        .tl-text {
          height: 50%;
          flex-shrink: 0;
          flex-grow: 1; } } } } }


// Slide content

.tl-slider-item-container {
  .tl-slide {
    .tl-slide-content-container {
      .tl-slide-content {
        display: flex !important;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100% !important;
        height: 100%;
        padding: rv(0) !important;

        @include bp($desktop) {
          flex-direction: row;
          height: 100%;
          padding: rv(1) !important; }

        &.order-reversed {
          flex-direction: column-reverse;

          @include bp($desktop) {
            flex-direction: row-reverse; } } } } } }


// Title slide

.tl-slide.tl-slide-titleslide {
  .tl-slide-content-container {
    .tl-slide-content {
     flex-direction: column; } } }

.tl-slide.tl-slide-titleslide {
  .tl-slide-content-container {
    .tl-text {
     width: 100% !important;
     max-width: 100% !important; } } }

.tl-timeline {
  .tl-storyslider {
    .tl-slider-container-mask {
      .tl-slider-container {
        .tl-slider-item-container {
          .tl-slide {
            .tl-slide-background {
              display: block;
              background: $black;
              opacity: 1; } } } } } } }

.tl-timeline {
  .tl-storyslider {
    .tl-slider-container-mask {
      .tl-slider-container {
        .tl-slider-item-container {
          .tl-slide-titleslide {
            .tl-slide-background {
              display: block;
              &:before {
                content: "";
                background: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0) 100%);
                height: 100%;
                display: block; } } } } } } } }

.tl-slide-content-container {
  .tl-text {
    .tl-text-content-container {
      .tl-text-content {
        text-align: left; }
      .tl-headline {
        border-bottom: 0;
        text-align: left;
        a {
          color: $white; } } } } }


.tl-slide-titleslide {
  .tl-slide-content {
    z-index: 3; } }

// General Text

.tl-text {
  p {
    font-family: $basefont;
    color: $white!important;
    @include font-size(-1);

    @include bp($stack) {
      @include font-size(0); } } }

.tl-text {
  .tl-text-content-container {

    h3.tl-headline-date {
      font-weight: $bold;
      text-align: left;
      color: $white;
      font-family: $headingfont;
      margin-bottom: 0;
      font-size: font-value(-1);
      text-decoration: underline; } } }


// Navigation
.tl-timeline:hover {
  .tl-slidenav-next {
    .tl-slidenav-icon {
      &:before {
        content: url(get_svg_url('icons/right_arrow_white_active')) !important; } } } }
.tl-timeline:hover {
  .tl-slidenav-previous {
    .tl-slidenav-icon {
      &:before {
        content: url(get_svg_url('icons/left_arrow_white_active')) !important; } } } }
.tl-slidenav-next {
  .tl-slidenav-icon {
    &:before {
      content: url(get_svg_url('icons/right_arrow_white')) !important; } } }

.tl-slidenav-previous {
  .tl-slidenav-icon {
    &:before {
      content: url(get_svg_url('icons/left_arrow_white')) !important; } } }

.tl-slidenav-icon {
  width: 100px;
  margin-left: 0 !important;
  margin-right: 0 !important; }

.tl-slidenav-title {
  opacity: 1!important;
  font-size: font-value(-1)!important;
  line-height: font-value(-1)!important;
  text-shadow: unset!important;
  color: $clr1!important;
  display: none!important; }

.tl-slidenav-description {
  display: none; }

// Title Slide

.tl-slide-titleslide {
  .tl-slide-content {
    text-align: left;
    span {
      color: $white;
      font-family: $basefont;
      font-weight: $bold;
      margin-top:  0;
      @include bp($stack) {
        margin-top: rv(1); } }
    h1, h2 {
      color: $white;
      font-size: $h2fontsize;
      line-height: $h2fontsize; } } }



// Time Markers

.tl-timenav-item-container {
  .tl-timemarker {
    .tl-timemarker-content-container {
      border-bottom-right-radius: 0; }
    .tl-timemarker-content {
      .tl-timemarker-text {
        .tl-headline {
          font-family: $headingfont;
          font-size: font-value(4);

          @include bp($stack) {
            font-size: font-value(2); } } } } } }

.tl-timemarker {
  .tl-timemarker-content-container {
    .tl-timemarker-content {
      .tl-timemarker-media-container {
        .tl-timemarker-media {
          opacity: 1!important; } } } } }

.tl-timemarker-text {
  .tl-headline {
    color: $black!important;
    font-size: font-value(-2)!important; } }

.tl-timemarker {
  .tl-timemarker-content-container {
    width: 200px!important; } }

.tl-timeaxis-tick {
  &:before {
    border-color: $black!important; } }

.tl-timeaxis-tick-text {
  color: $black!important; }

.tl-slide {
  &.tl-slide-titleslide {
    background: $black; } }
