// $clr4_map: (dark: #283A44, shade: #3C5766, primary: $clr4, tint: #9FC4DB, fade: #C5DCE9, watermark: #ECF3F8)



// Neutrals
// neutral() function mixes blackpoint and whitepoint.

$lightest_neutral: neutral(5);
$lighter_neutral: neutral(30);
$light_neutral: neutral(40);
$neutral: neutral();
$mid_neutral: neutral(60);
$dark_neutral: neutral(70);
$darker_neutral: neutral(80);
$darkest_neutral: neutral(90);

// Background

$bgcolor: $white;
$bgcolor2: #FCFADE;
$bgcolor3: #FFF7E9;
$bgcolor4: #DEC4BB;
$bgcolor5: #D9D8D7;
$bgcolor6: #F7F6F5;



// Aliases

$impact: $clr2;
$cta: set-color(4,'dark');
$de_emphasized: $light_neutral;
$de_activated: $lighter_neutral;
$link_colour: $black;

// !LOCAL

// Gradients

$dark_interior: darken(set-color(4,'dark'),20%);

$ltr_dark_gradient: linear-gradient(270deg, rgba($black, 0.8) 36%, rgba($dark_interior,0.7) 100%);
$rtl_dark_gradient: linear-gradient(90deg, rgba($black, 0.8) 36%, rgba($dark_interior,0.7) 100%);
$home_rtl_dark_gradient: linear-gradient(90deg, rgba($dark_interior, 0.7) 36%, rgba($clr1,0.1) 100%);
$mirror_gradient: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba(0,0,0,.5) 15%, rgba(159,159,159,0) 50%, rgba(0,0,0,.5) 85%, rgba(0,0,0,1) 100%);
$mirror_gradient_green: linear-gradient(90deg, rgba(0,0,0,1) 0%, rgba($clr1,50%) 50%, rgba(0,0,0,1) 100%);



