/* Sets standard viewport to the device-width */

@mixin device-width-viewport() {
  @-webkit-viewport {
    width: device-width; }

  @-moz-viewport {
    width: device-width; }

  @-ms-viewport {
    width: device-width; }

  @-o-viewport {
    width: device-width; }

  @viewport {
    width: device-width; } }
