/*
   @file _slick.sass
   @theme Local
   @description  Loads SASS files for local theme. */

$dot_size: rv(0);  // size of slider nav dots
$slide_content_margin: rv(0);


.slideshow-block, .slideshow-with-audio {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: calc(100vw - 3.0rem); // Cheat
  height: 90vh;

  @include bp($stack) {
    max-height: 800px;
    max-width: initial; } }

.slideshow-with-audio {
  display: flex;
  flex-direction: column; }

.slideshow, .slick-list, .slick-track {
  height: 100%;
  overflow: hidden;
  width: 100%; }


.slideshow {
  background: $black;

  .item {
    display: flex !important;
    flex-direction: column;
    justify-content: stretch;
    height: 100%;

    figure {
      position: relative;
      height: 100%;
      flex-shrink: 1;
      min-height: 40%;

      &.figure-portrait {
        padding: rv(0) 0;

 }        // Overlay for nav arrow background

      &:before {
        opacity: 0;
        position: absolute;
        content: '';
        background: $mirror_gradient;
        width: 100%;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        pointer-events: none;
        @include transition(opacity,-3);

        @include bp($stack) {
          height: 100%!important;
          max-height: 100%!important; } } }

    &.introduction {
      > figure {
        background-color: $clr1;

        img {
          mix-blend-mode: multiply;
          filter: grayscale(100%);
          position: relative;
          display: block;
          background-color: transparent;

          &:hover {
            filter: grayscale(0%); } } } }

    img {
      height: 100%;
      object-fit: cover;
      //filter: grayscale(100%)

      &.img-portrait {
        object-fit: contain; } } }

  // makes nav arrow background visible

  &.navHover {
    .item {
      figure:before {
        opacity: 1; } } } }

// Safari flickers when this transition is applied. So far no solution is apparent.
// This ugly hack will allow this feature to gracefully degrade on Safari.

_::-webkit-full-page-media, _:future, :root .slideshow .item figure:before {
  transition: none; }


.slide-contents {
  flex-shrink: 0;
  padding: $slide_content_margin $slide_content_margin rv(-1) + $dot_size + $slide_content_margin $slide_content_margin;
  color: $white;
  border-top: 6px solid $white;

  h3 {
    color: $white;
    font-family: $headingfont;
    margin: 0 0 rv(-2) 0;
    font-size: font-value(-1);

    @include bp($stack) {
      font-size: font-value(2); }

    a {
      color: $white !important;
      text-decoration: underline; } }

  p {
    font-size: font-value(-1); }

  &:last-child {
    margin-bottom: 0; } }


// !SPLASH VERSION

#splash {

  .slideshow-block {
    border-bottom-right-radius: 0;
    &:before {
      content: unset; } }

  .slideshow-block, .slideshow-with-audio {
    max-width: 100%;
    max-height: 100%;
    border-top: 0;

    .slideshow {
      border-top: 0; } }

  // !Splash Title Page

  .introduction {

    .slide-contents {
      // background: unset
      // position: absolute
      z-index: 100;
      // +splash_area_title_left(70%)
      border-top: 5px solid;
      background: $black;

      h3 {
        text-transform: uppercase;
        color: $white;

        a {
          color: $white!important; } }
      p {
        color: $white; } } }

  .slide-contents, .audio-controls {

    @include bp($stack) {
      padding-left: $title_area_outside_padding;
      padding-right: $title_area_outside_padding; } }

  h3 {
    font-size: +font-value(1); } }


// !UI CONTROLS
.fullscreen-label {
  text-align: left;
  font-size: font-value(-2);
  line-height: font-value(-1);
  position: absolute;
  left: -68px; }

.slide-fullscreen-openBtn {
  position: absolute;
  top: rv(0);
  right: rv(0);
  color: $white;
  border: 0;
  border-radius: 0;
  height: 25px;
  width: 25px;
  min-width: auto;
  background: url(../../asset/img/svg/icons/fullscreen_white_icon.svg) no-repeat;

  &:hover {
    border: 0;
    border-radius: 0;
    background: url(../../asset/img/svg/icons/fullscreen_yellow_icon.svg) no-repeat;
    background-color: transparent;
    color: $clr2; } }

.slide-fullscreen-closeBtn {
  position: absolute;
  top: 0;
  right: 0;
  color: $white;
  border-radius: 100px;
  background: none;
  border: 2px solid $white;
  width: 30px;
  min-width: auto;
  height: 30px;
  margin: 10px;
  padding: 0;
  &:hover {
    border: 2px solid $white;
    background: unset;
    color: $white; } }

.slideshow-block, .slideshow-with-audio {
  border-bottom-right-radius: $borderRadius;
  position: relative;
  border-top: rv(0) solid $black;

  .slideshow {
    border-top: 6px solid $white; } }

// !Slick Controls

.slick-prev {
  position: absolute;
  width: rv(4);
  height: rv(4);
  top: 40%;
  transform: translateY(-50%);
  left: 20px;
  z-index: 100;
  cursor: pointer;
  margin: 0;
  background: url("../../asset/img/svg/icons/left_arrow_white_active.svg") no-repeat;
  &:hover {
    background: url("../../asset/img/svg/icons/left_arrow_yellow.svg") no-repeat; } }

.slick-next {
  position: absolute;
  width: rv(4);
  height: rv(4);
  top: 40%;
  transform: translateY(-50%);
  right: 15px;
  z-index: 100;
  cursor: pointer;
  margin: 0;
  background: url("../../asset/img/svg/icons/right_arrow_white_active.svg") no-repeat;
  &:hover {
    background: url("../../asset/img/svg/icons/right_arrow_yellow.svg") no-repeat; } }


.slick-dots {
  position: absolute;
  text-align: right;
  height: $dot_size;
  column-count: unset;
  top: initial;
  bottom: $slide_content_margin;
  right: $slide_content_margin;
  padding: 0;
  z-index: 100;
  margin: 0;
  display: flex;
  justify-content: flex-end;

  li.slick-active {
    &:after {
      color: $clr2; } }

  li:hover {
    &:after {
      color: $clr2; } }

  li {
    width: $dot_size;
    display: flex;
    cursor: pointer;
    height: 100%;
    padding-left: $dot_size;
    background: url("../../asset/img/svg/icons/two_dashes.svg") no-repeat;
    background-size: $dot_size;
    background-position: 4px center; // not sure where the fudge factor comes from. may be compensating for original image.

    &:after {
      content: " ";
      position: absolute;
      display: block;
      color: neutral(25);
      width: $dot_size;
      height: $dot_size;
      position: relative;
      border-radius: 50%;
      border: 3px solid;
      box-sizing: border-box;
      right: 0; }

    &:first-child {
      background: none;

 } }      // background: url("../../asset/img/svg/icons/two_dashes.svg") no-repeat
  button {
    display:none!important {} } }



// !AUDIO PLAYER
.audio-controls > .audio-player > .media-render > audio {
  display: none; }
.audio-controls {
  width: 100%;
  background: #34423D;
  color: $white;
  border-top: rv(-5) solid $white;
  margin: 0;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: rv(-4);
  padding-bottom: rv(-4);

  @include bp($stack) {
    display: flex;
    padding-top: rv(-4);
    padding-bottom: rv(-4);
    flex-direction: row;
    justify-content: flex-start;
    align-content: center; } }

.audio-player, .audio-player > *, .audio-caption {
  display: flex;
  height: 100%;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin: 0;
  padding-right: rv(1);
  font-family: $headingfont;

  a {
    color: $white;
    font-size: font-value(-2);
    outline: 0;
    font-family: $labelfont;
    font-weight: $bold;
    &:hover {
      color: $clr1; }
    &::before {
      content: " ";
      background: url("../../asset/img/svg/icons/sound.svg") no-repeat;
      position: absolute;
      width: 25px;
      height: 25px;
      margin-left: -34px; } } }


.audio-caption {
  display: none;

  @include bp($stack) {
    display: flex; }

  p {
    margin: 0;
    font-size: font-value(0);
    line-height: 1.4rem;
    margin-bottom: 3px; } }





