/**
 *  @file _poster.sass
 *  @category 60_site_elements
 *  @description Styling for poster block */


// @todo: move main spacing to layout and refactor !important

main {
  .poster {
    margin: rv(1) 0 !important;

    @include bp($stack) {
      margin: rv(2) 0 !important; }

    @include bp($desktop) {
      margin: rv(4) 0 !important; } } }

.poster {
  min-height: 34rem;
  width: 100%;
  padding: rv(3) !important;
  position: relative;

  @include bp($stack) {
    min-height: 28rem; }

  @include bp($tablet) {
    padding: rv(4) !important;
    padding-right: rv(5) !important; }

  @include bp($desktop) {
    padding: rv(5) !important;
    padding-right: rv(6) !important; }

  &:after {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    content: " ";
    filter: grayscale(0);
    background-repeat: no-repeat;
    background-position: bottom rv(0) right rv(0);
    background-size: rv(2) rv(2);

    @include bp($stack) {
      background-size: rv(3) rv(3); }

    @include bp($desktop) {
      background-size: rv(5) rv(5); } }

  .image-background {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    figure {
      height: 100%; } }

  img {
    border-top-right-radius: $borderRadius;
    filter: grayscale(100%);
    mix-blend-mode: multiply;
    object-fit: cover;
    height: 100%; }

  .poster-text {
    position: relative;
    z-index: 1; }

  &.colour-scheme-1 {
    background: $clr1;
    border-top-right-radius: $borderRadius;

    &:after {
      content: " ";
      background-image: url("../../asset/img/svg/marks/icon_trans_white_mark.svg"); }

    .poster-text {
      color: $white; } }

  &.colour-scheme-2 {
    &:after {
      background-image: url("../../asset/img/svg/marks/icon_trans_yellow_mark.svg"); }

    background: $clr1;
    border-top-right-radius: $borderRadius;
    .poster-text {
      color: $clr2; } }

  &.colour-scheme-3 {
    &:after {
      background-image: url("../../asset/img/svg/marks/icon_trans_white_mark.svg"); }
    background: $clr3;
    border-top-right-radius: $borderRadius;
    .poster-text {
      color: $white; } }

  &.colour-scheme-4 {
    &:after {
      background-image: url("../../asset/img/svg/marks/icon_trans_yellow_mark.svg"); }
    background: $clr4;
    border-top-right-radius: $borderRadius;
    .poster-text {
      color: $clr2; } }

  &.text-treatment-1 {
    .poster-text {
      p {
        font-family: $headingfont;
        font-weight: $heavy;
        font-size: font-value(1);
        line-height: font-value(2);
        @include bp($tablet) {
          font-size: font-value(2);
          line-height: font-value(3); }
        @include bp($desktop) {
          font-size: font-value(4);
          line-height: font-value(5); } } } }


  &.text-treatment-2 {}
  .poster-text {
    p {
      font-family: $codefont;
      font-weight: $heavy;
      font-size: font-value(0);
      line-height: font-value(1);
      @include bp($tablet) {
        font-size: font-value(1);
        line-height: font-value(2); }
      @include bp($desktop) {
        font-size: font-value(2);
        line-height: font-value(3); } } } }
