.introductory-text {
  @include fluid-type(-1,1);
  height: 100%;
  margin-right: 0;
  margin-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  z-index: 2;
  flex: 1;
  font-weight: $bold;

  @include bp($stack) {
    font-weight: $normal;
    border-bottom: 0;
    border-left: 0; }

  * {
    font-size: 0.9em;

    @include bp($tablet) {
      font-size: 0.95em; }

    @include bp($desktop) {
      font-size: 0.85em; } }

  h1 {
    font-size: 2.4em;
    margin: 0;
    padding: 0 0 rv(0) 0; } }


#homepage-splash, .section-intro-splash {

  figcaption {
    top: 0;
    position: absolute;
    color: $white;
    padding: rv(-1);
    z-index: 9;
    font-style: normal;
    font-weight: $bold;

    @include bp($stack) {
      padding-top: rv(0);
      padding-left: rv(0);
      max-width: 70%; }

    p {
      font-size: font-value(-1);
      line-height: font-value(0);

      @include bp($stack) {
        font-size: font-value(0);
        line-height: font-value(1); } } } }


.section-intro-splash {
  margin-left: 0;
  margin-right: 0;
  width: 100%;
  max-width: 100%;
  position: relative;
  display: flex;
  flex-direction: column-reverse;
  overflow: hidden;

  @include bp($stack) {
    background: $black; }

  .items {
    @include splash_area_carousel_left(); }

  .introduction {
    @include splash_area_title_right();
    background-color: $black; }

  .introductory-text {
    * {
      color: $white; } }


  figure {
    position: relative;

    figcaption {
      position: absolute;
      color: $white;
      padding-left: rv(-1);
      z-index: 9;
      font-style: normal;
      max-width: 70%;
      padding-top: rv(-1);
      font-weight: $bold;

      @include bp($stack) {
        padding-top: rv(0);
        padding-left: rv(0);
        max-width: 300px; }

      p {
        line-height: font-value(0);
        font-size: font-value(-1);
        @include bp($stack) {
          line-height: font-value(1);
          font-size: font-value(0); } } }

    a {
      display: block;
      height: 100%;
      width: 100%;
      background: set-color(5,'tint');
      img {
        filter: grayscale(100%);
        mix-blend-mode: multiply;
        object-fit: cover;
        height: 340px;
        @include bp($stack) {
          height: unset; } } }

    &:after {
      position: absolute;
      content: "";
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      opacity: 1;

      @include bp($stack) {
 } } }       // background: $ltr_dark_gradient

  .slick-next, .slick-prev {
    top: 47%; }
  .slick-next {
    @include bp($stack) {
      right: 19px; } }

  &.left {
    &:after {
      background: $rtl_dark_gradient; } } }


.section-intro-splash {
  &.right {
    .introduction {
      border-bottom-left-radius: $borderRadius; } } }
.section-intro-splash {
  &.left {
    .items {
      align-self: flex-end; }
    .introduction {
      border-bottom-right-radius: $borderRadius; } } }
