/**
 * @file _font_size.sass
 * @category 20_typography
 * @group a_components
 * @description Creates a font-size declaration based on relative value $factor, $basetypesize and $scale.
 *
 */

@mixin font-size($factor: 0,$ratio: $scale) {
  font-size: font-value($factor,$ratio); }

// Set notation

@mixin set-font-size($factor: 0,$ratio: $scale) {
  @include font-size($factor,$ratio); }

// Unscaled font-size

@mixin base-font-size() {
  font-size: $basetypesize; }

// Alias of above

@mixin base-type-size() {
  @include base-font-size(); }
